.notice-goblog-free {
    padding: 20px;

    h2 {
        font-size: 20px;
        margin: 0 0 .8em;
    }

    p {
        font-size: 14px;
        margin: 0;
        margin-bottom: 8px;
        padding: 0;
        line-height: 1.2;
    }

    .box-btn {
        margin-top: 15px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 12px;

        a {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 6px;
            padding: 6px 14px;
            font-weight: 600;
            background: #4df409;
            border-radius: 20px;
            text-transform: capitalize;
            color: #4df409;
            line-height: 1.6;
            text-decoration: none;

            svg {
                width: 20px;
                width: 20px;
                fill: #4df409;
            }
        }

        a.usage-guide {
            color: #222;

            svg {
                fill: #222;
            }
        }

        a.hire-developer {
            background: linear-gradient(90deg,#072a8c,#7b05aa,#072a8c);
            background-size: 200% 200%;
            animation: block-icons-admin-notices 2.5s ease infinite;
        }

        @keyframes block-icons-admin-notices {

            0% {
                background-position: 0% 50%;
            }

            50% {
                background-position: 100% 50%;
            }

            100% {
                background-position: 0% 50%;
            }
        }
    }
}